@import "../../../scss/Colors";

.topbar {
  font-family: Helvetica;
  font-weight: bold;
  font-size: 1.3rem;
  background-color: $ml_grey;
  max-height: 100px;
  border-bottom: 1px solid #fff;

  a {
    color: #fff;

    &:hover {
      color: $ml_yellow;
    }

    &:active {
      color: $ml_yellow;
    }
  }

  & img {
    height: 70px;

    &:hover {
      animation: tilt_logo infinite 6s;
    }
  }
}

.navbar {
  margin-left: auto;

  & li {
    padding: 0px 40px 0px 0px !important;
  }

  &-toggler {
    background-color: $ml_yellow !important;
    border: 1px solid #f00 !important;
  }
}

.dropdown_menu {
  background-color: $ml_blue !important;
}

.anim_text {
  color: #fff;

  &:hover {
    animation: text_showing infinite 6s;
  }
}

@keyframes tilt_logo {
  0% {
    margin: 0;
  }

  5% {
    -webkit-transform: rotate(-5deg);
  }

  100% {
    -webkit-transform: rotate(-5deg);
  }
}

@keyframes text_showing {
  0% {
    left: -200px;
  }

  5% {
    left: 300px;
  }

  100% {
    left: 300px;
  }
}
