@import "../../../scss/Colors";

.contacts {
  color: #fff;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 30px;
  display: inline-block;
  text-align: center;

  & span {
    padding-left: 0.5rem;
  }

  & img {
    width: 100%;
  }

  & a {
    color: $ml_blue;

    &:hover {
      color: $ml_yellow;
    }
  }
}
