@import "../../../scss/Colors";

.footer {
  font-family: Helvetica;
  font-size: 0.9rem;
  background-color: $ml_grey;
  height: 25px;
  border-top: 1px solid #fff;
  color: #fff;

  &_copyright {
    margin-left: auto;

    & > span {
      color: #fff;
    }

    & svg {
      margin-left: 1rem;
      max-height: 1.2rem;
      color: #fff;
    }
  }

  a {
    color: #fff;

    &:hover {
      color: $ml_yellow;
    }

    &:active {
      color: $ml_yellow;
    }

    & svg:hover {
      color: $ml_yellow;
    }
  }
}

@media (max-width: 768px) {
  .footer {
    padding-bottom: 10px !important;
  }
}
