@import "../../scss/Colors";
@import "../../scss/index.scss";

.fullsize {
  width: 100%;
  height: 100%;

  & h1 {
    font-style: oblique;
    font-weight: bold;
    font-size: 3.75rem;
    text-align: center;
    color: #fff;
    margin-top: 2%;
  }

  & h2 {
    font-size: 3rem;
  }

  & h3 {
    margin: 20px;
  }
}

.white {
  background-color: #fff;
  height: 75vh;

  & > .animation_text {
    margin: 30px;
  }
}

#animation {
  width: 100vw;
  height: 30vh;
  background: #fff url("https://media.giphy.com/media/12MhwQm8toOEp2/giphy.gif") repeat-y;
  animation: play 8s steps(120) infinite;
  display: inline-block;
}

@keyframes play {
  0% {
    background-position-x: -2000px;
  }

  50% {
    background-position-x: 0px;
  }

  100% {
    background-position-x: 2000px;
  }
}
