@import "../../../scss/Colors";

.btn {
  color: #000;
  background: $ml_yellow;
  font-weight: 700;
  text-align: right;
  text-decoration: none;
  border: 0;
  outline: 0;
  font-size: 2rem;
  padding: 20px 40px;
  display: inline-block;
  transform: skew(-18deg);
  font-style: oblique;

  &:hover {
    background: $ml_blue;
    color: #fff;
  }

  & > span {
    display: inline-block;
    transform: skew(21deg);
  } /* Un-skew the text */
}

.budget_button {
  position: fixed;
  top: 25%;
  left: 96%;
  transform: translate(-70%, -90%);
  z-index: 99;
}

@media (max-width: 768px) {
  .budget_button {
    display: none !important;
  }
}
