@import "../../../scss/Colors";

#button {
  background-color: #fff;
  width: 5rem;
  height: 5rem;
  margin-left: 1rem;
  position: fixed;
  bottom: 2.5rem;
  right: 3rem;
  z-index: 99;
  color: $ml_grey;
  border-radius: 1rem;
  padding: 0.4rem;
  cursor: pointer;
  border: 0;
  opacity: 0.2;
  display: block;

  &:hover {
    opacity: 1;
  }
}

@media (max-width: 768px) {
  #button {
    display: none !important;
    color: $ml_yellow;
  }
}
